@font-face {
  font-family: "TT Norms";
  src: local("TT Norms Bold"), local("TTNorms-Bold"), url("../fonts/TTNorms-Bold.woff2") format("woff2"), url("../fonts/TTNorms-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "TT Norms";
  src: local("TT Norms Medium"), local("TTNorms-Medium"), url("../fonts/TTNorms-Medium.woff2") format("woff2"), url("../fonts/TTNorms-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "TT Norms";
  src: local("TT Norms Regular"), local("TTNorms-Regular"), url("../fonts/TTNorms-Regular.woff2") format("woff2"), url("../fonts/TTNorms-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.body {
  font-family: 'TT Norms', 'Times New Roman', serif;
  font-size: 18px;
  line-height: 21px;
  color: #b3b3ba;
  margin: 0 auto;
  position: relative;
  scroll-behavior: smooth;
}

.body_white {
  color: #ffffff;
}

.body_black {
  color: #000000;
}

.body__service {
  position: absolute;
  left: 0;
  width: 100%;
  height: 500px;
  object-fit: cover;
  object-position: 50% 50%;
  z-index: -1;
}

.wrapper {
  max-width: 1440px;
  min-width: 375px;
  margin: 0 auto;
}

.wrapper-top {
  margin: 0 auto;
  background-image: linear-gradient(42.01deg, #323264 14.33%, #323264 51.39%, #7a425e 87.53%);
}

.wrapper-bottom {
  background: linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643e72 87.46%);
  border-radius: 0 0 12px 12px;
  position: relative;
  background-repeat: no-repeat;
  background-size: 100%;
}

.wrapper-bottom::after,
.wrapper-bottom::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  background-color: #cacadb;
  width: calc(100% - 40px);
  height: 10px;
  border-radius: 0 0 12px 12px;
}

.wrapper-bottom::before {
  bottom: -20px;
  background-color: #e7e7f2;
  width: calc(100% - 80px);
}

.container {
  max-width: 1360px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}

@media (min-width: 1200px) {
  .container {
    padding: 0 20px;
  }
}

.container-min {
  max-width: 1090px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container-min {
    padding: 0 40px;
  }
}

@media (min-width: 1100px) {
  .container-min {
    padding: 0;
  }
}

.abr {
  text-transform: uppercase;
}

.btn {
  background: #24a3ff;
  box-shadow: 0 0 20px rgba(36, 163, 255, 0.5);
  border-radius: 12px;
  border: none;
  cursor: pointer;
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  padding: 17px 78px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.btn:hover {
  background: #0085ff;
}

.btn:active {
  background: #006ccf;
}

.dots {
  display: flex;
  justify-content: center;
  padding-top: 19px;
  position: relative;
  left: 52%;
  transform: translateX(-50%);
}

.dots__item {
  width: 16px;
  height: 16px;
  background: #24a3ff;
  border-radius: 50%;
  opacity: .2;
  margin-right: 14px;
}

.dots__item:hover {
  opacity: .6;
}

.dots__item-active {
  opacity: 1;
}

.services-box-icon {
  display: block;
  text-align: center;
  background-color: #ffffff;
  padding: 7px 11px;
  border-radius: 18px;
}

@media (min-width: 992px) {
  .services-box-icon {
    padding: 12px 14px;
    border-radius: 24px;
  }
}

.animation_link {
  color: black;
  position: relative;
  display: inline-block;
  outline: none;
}

.animation_link, .animation_link:focus {
  outline: none;
}

.animation_link:hover::after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 4px;
  background: rgba(0, 0, 0, 0.1);
  content: '';
}

.close {
  position: relative;
  top: 5px;
  right: 5px;
  width: 18px;
  height: 18px;
  left: 88%;
  cursor: pointer;
}

.close:before,
.close:after {
  content: "";
  position: absolute;
  top: 21px;
  left: 10px;
  width: 18px;
  height: 4px;
  background: #f0145a;
}

.close:before {
  transform: rotate(45deg);
}

.close:after {
  transform: rotate(-45deg);
}

