}

#mobile-menu__toggle {
  opacity: 0;
}

#mobile-menu__toggle:checked ~ .mobile-menu__btn > span {
  transform: rotate(45deg);
  background-color: transparent;
}

#mobile-menu__toggle:checked ~ .mobile-menu__btn > span::before {
  top: -56px;
  right: 62px;
  width: 25px;
  background-color: #f0145a;
  transform: rotate(0);
}

#mobile-menu__toggle:checked ~ .mobile-menu__btn > span::after {
  top: -56px;
  right: 62px;
  width: 25px;
  background-color: #f0145a;
  transform: rotate(90deg);
}

#mobile-menu__toggle:checked ~ .mobile-menu__box {
  visibility: visible;
}

.mobile-menu {
  position: relative;
  right: -7px;
  z-index: 10;
}

.mobile-menu__btn {
  display: flex;
  align-items: center;
  width: 18px;
  height: 18px;
  cursor: pointer;
  z-index: 20;
  position: absolute;
  top: -29px;
  right: -1px;
}

.mobile-menu__btn_about {
  right: 4px;
}

.mobile-menu__btn > span,
.mobile-menu__btn > span::before,
.mobile-menu__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
}

.mobile-menu__btn_blue > span,
.mobile-menu__btn_blue > span::before,
.mobile-menu__btn_blue > span::after {
  background-color: #323264;
}

.mobile-menu__btn:active > span,
.mobile-menu__btn:active > span::before,
.mobile-menu__btn:active > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #323264;
}

.mobile-menu__btn > span::before {
  content: '';
  top: -6px;
}

.mobile-menu__btn > span::after {
  content: '';
  top: 6px;
}

.mobile-menu__box {
  display: block;
  position: fixed;
  visibility: hidden;
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 80px 0;
  transition-duration: .1s;
}

.mobile-menu__box-ul {
  list-style: none;
  text-align: center;
  margin: 0;
  padding: 0;
}

.mobile-menu__item {
  display: block;
  padding: 12px 24px;
  color: #24a3ff;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-decoration: none;
  background-color: #f5f5fa;
  margin-bottom: 1px;
  transition-duration: .1s;
}

.mobile-menu__box::before {
  content: url("../img/icons/mob-menu-word.svg");
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  height: 53px;
  left: 0;
  top: 0;
  padding-top: 27px;
  z-index: 15;
  transition-duration: .1s;
}

.mobile-menu__item:hover {
  color: #f0145a;
}

@media (min-width: 768px) {
  .mobile-menu__btn {
    display: none;
  }
  .mobile-menu__box {
    width: 430px;
    visibility: visible;
    position: absolute;
    padding: 0;
    top: 11px;
    left: -6px;
  }
  .mobile-menu__box::before {
    display: none;
  }
  .mobile-menu__box-ul {
    list-style: none;
    text-align: center;
    display: flex;
  }
  .mobile-menu__item {
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    background: none;
    padding: 0;
    margin-right: 16px;
  }
  .mobile-menu__item:hover {
    color: #f0145a;
    position: relative;
  }
}

@media (min-width: 992px) {
  .mobile-menu__box {
    padding: 0;
    visibility: visible;
    position: absolute;
    top: -48px;
    left: calc(100% - 410px);
  }
  .mobile-menu__item {
    color: #ffffff;
  }
  .mobile-menu__item_blue {
    color: #24a3ff;
  }
  .mobile-menu__item_current {
    color: #f0145a;
    position: relative;
  }
  .mobile-menu__item_current::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #f0145a;
    bottom: -2px;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (min-width: 1200px) {
  .mobile-menu__box {
    top: -48px;
  }
}

