.service-form {
  display: none;
  font-family: "TT Norms", "Times New Roman", serif;
  width: 527px;
  position: fixed;
  left: 50%;
  top: 50%;
  background-size: cover;
  transform: translateX(-50%) translateY(-50%);
  z-index: 99999;
}

.service-form::before {
  content: '';
  background: #323264;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  z-index: 2;
  transform: translateZ(-10px);
}

.service-form__body {
  background-color: #f5f5fa;
  border-radius: 12px;
  padding: 0 0 20px 20px;
