  margin-bottom: 20px;
}

.maintenance__text {
  line-height: 26px;
  color: #ffffff;
  margin: 21px 0 0 0;
}

.maintenance__btn {
  margin: 37px 0 38px;
  padding: 17px 22px;
}

.maintenance__bottom-title {
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  margin: 0;
  padding-bottom: 11px;
}

.maintenance__bottom-list {
  margin: 0;
  padding: 0 0 0 25px;
  list-style-type: none;
}

.maintenance__bottom-li {
  font-size: 17px;
  line-height: 26px;
  color: #ffffff;
  position: relative;
  padding-bottom: 15px;
}

.maintenance__bottom-li::before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #f0145a;
  border-radius: 50%;
  position: absolute;
  left: -14px;
  top: 11px;
}

@media (min-width: 768px) {
  .maintenance {
    padding-top: 40px;
  }
}

@media (min-width: 992px) {
  .maintenance {
    padding-top: 4px;
  }
  .maintenance__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 40px;
  }
  .maintenance__text {
    max-width: 830px;
    text-align: center;
    font-size: 21px;
    line-height: 30px;
  }
  .maintenance__btn {
    position: relative;
    left: 130px;
  }
  .maintenance__bottom {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 64px;
  }
  .maintenance__bottom-title {
    font-size: 24px;
    line-height: 28px;
    flex-basis: 100%;
    padding-bottom: 20px;
  }
  .maintenance__bottom-list {
    width: 50%;
  }
  .maintenance__bottom-li {
    font-size: 17px;
    line-height: 26px;
    color: #ffffff;
    position: relative;
    padding-bottom: 15px;
  }
}
